import React from "react";
import Calendar from "react-calendar";
function DatePicker({ show, id, setDate, date, minDate, dateDisable }) {
  return (
    <div id={id} className="absolute z-10 r-calendar">
      <Calendar
        locale="en-US"
        onChange={setDate}
        value={date}
        calendarType={"iso8601"}
        onClickDay={(e) => {
          show(false);
        }}
        tileDisabled={
          dateDisable ? ({ date }) => [1, 2, 3, 4].includes(date.getDay()) : null
        }
        minDate={minDate}
      />
    </div>
  );
}

export default DatePicker;
