import React, { useState, useRef, useEffect } from "react";
import Select from "react-select";
import { useHydrated } from "remix-utils";
import DatePicker from "./datePicker";
import {
  format,
  endOfYear,
  startOfISOWeek,
  getDay,
  addYears,
  startOfWeek,
  addDays,
  getWeek,
} from "date-fns";
import { toast } from "react-toastify";
import { directusLoaderLink } from "../../utils/constants";
import ConfirmationModal from "../Modal/confirmationModal";
const ClickOutsideDiv = ({
  Id,
  startDate,
  setRefresh,
  refresh,
  locationId,
  type,
}) => {
  const DateFormat = "yyyy-MM-dd";
  const options = [
    { name: "Do not repeat", id: 0 },
    { name: "Weekly", id: 1 },
    { name: "Monthly", id: 2 },
    { name: "Annually", id: 3 },
    { name: "Custom", id: 4 },
  ];
  const days = [
    { name: "Monday", id: 1 },
    { name: "Tuesday", id: 2 },
    { name: "Wednesday", id: 3 },
    { name: "Thursday", id: 4 },
    { name: "Friday", id: 5 },
    { name: "Saturday", id: 6 },
    { name: "Sunday", id: 0 },
  ];
  const [isDivVisible, setDivVisible] = useState(false);
  const divRef = useRef(null);
  const isHydrated = useHydrated();
  const [calenderShow, setCalenderShow] = useState(false);
  const [calenderShow1, setCalenderShow1] = useState(false);
  const [calenderShow2, setCalenderShow2] = useState(false);
  const [week, setWeek] = useState("");
  const [calStartDate, setCalStartDate] = useState("");
  const [calEndDate, setCalEndDate] = useState("");
  const [daysCopy, setDaysCopy] = useState(options[0]);
  const [Occurrence, setOccurrence] = useState(false);
  const [daysSelect, setDaysSelect] = useState([]);
  const [modalIsOpen, setIsOpen] = useState(false);
  const [customTrue, setCustomTrue] = useState(false);
  const [screenId, setScreenId] = useState("");
  const [presentData, setPresentData] = useState("");
  const [modalLoader, setModalLoader] = useState(false);
  const [doneLoader, setDoneLoader] = useState(false);
  // Add an event listener to handle clicks outside of the div block
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (divRef.current && !divRef.current.contains(event.target)) {
        setDivVisible(false);
        setCalenderShow(false);
        setOccurrence(false);
        cancelHandle();
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  const toggleDiv = () => {
    setDivVisible(!isDivVisible);
    setCalenderShow(false);
    setCalenderShow1(false);
    setCalenderShow2(false);
    setDaysCopy(options[0]);
  };
  const RepeatSelection = (prop) => {
    const onChangeHandle = (e) => {
      prop == false && setCustomTrue(false);
      setOccurrence(prop ? true : e?.id === 4);
      setDaysCopy(e?.id === 4 ? options[1] : e);
      return "";
    };
    return (
      <Select
        id={Id}
        instanceId={Id}
        classNamePrefix="cursor-pointer react-select"
        className=" react-select-container font-normal text-sm w-full"
        maxMenuHeight={250}
        onChange={(e) => onChangeHandle(e)}
        isSearchable={false}
        components={{ IndicatorSeparator: () => null }}
        value={customTrue === true && prop === false ? options[4] : daysCopy}
        options={prop ? options.slice(1, 4) : options}
        getOptionLabel={(option) => option.name}
        getOptionValue={(option) => option.id}
        styles={{
          control: (baseStyles) => ({
            ...baseStyles,
            border: "none",
            boxShadow: "none",
          }),
        }}
      />
    );
  };
  const DatePickerComponent = ({ show, setShow, date, setDate, minDate }) => {
    return (
      show && (
        <div className="day-calendar absolute">
          <DatePicker
            id={Id}
            show={setShow}
            setDate={setDate}
            date={date !== "" ? date : startDate}
            minDate={minDate}
            dateDisable={type === "activity" ? true : false}
          />
        </div>
      )
    );
  };

  const daysHandle = (e) => {
    if (!daysSelect.includes(e?.id)) {
      setDaysSelect(
        daysSelect?.length ? [...daysSelect, e?.id]?.sort() : [e?.id]
      );
    } else {
      setDaysSelect(daysSelect.filter((l) => l !== e?.id)?.sort());
    }
  };
  const DaysSelection = () => {
    return days.map((e, i) => (
      <div
        key={i}
        onClick={() => (type === "activity" && i < 4 ? "" : daysHandle(e))}
        className={` ${
          daysSelect.includes(e?.id)
            ? "bg-primary text-white"
            : "bg-transparent border border-primary text-black"
        } text-center capitalize w-[28px] h-[28px] flex items-center justify-center rounded-[4px]
        ${
          type === "activity" && i < 4
            ? " bg-slate-400 border-slate-600 text-black cursor-default"
            : "cursor-pointer"
        } 
        `}
      >
        {e?.name?.charAt(0)}
      </div>
    ));
  };
  const cancelHandle = () => {
    setIsOpen(false);
    setOccurrence(false);
    setDaysCopy(options[0]);
    setCalStartDate("");
    setCalEndDate("");
    setDaysSelect([]);
    setWeek("");
  };
  const UrlDate = (prop) => {
    const date = format(prop, DateFormat);
    return date;
  };
  const closeCheck =
    daysCopy?.id == 0
      ? calStartDate === "" ||
        (calStartDate !== ""
          ? UrlDate(calStartDate) == UrlDate(startDate)
          : false)
        ? false
        : true
      : true;
  const ScreenIdGet = async () => {
    setDoneLoader(true);
    const getAllStations = await directusLoaderLink
      .items("Stations")
      .readByQuery({
        fields: ["*.*.*", "id", "station_name"],
        filter: {
          is_parent_station: { _neq: true },
        },
      })
      .then((e) => e?.data);
    const updateStationIds = [];
    const createStationIds = [];

    const ScreenDisplayRecipeId = await directusLoaderLink
      .items("screen_display")
      .readByQuery({
        fields: ["*.*.*"],
        filter: {
          date: UrlDate(startDate),
          location_id: locationId,
        },
      })
      .then((e) => {
        return e?.data;
      })
      .catch((e) => console.log(e));
    getAllStations?.map((e) => {
      const val = ScreenDisplayRecipeId?.filter(
        (val) => val?.station_name?.station_unique_id === e?.station_unique_id
      );
      val?.[0]?.id > 0
        ? updateStationIds?.push({
            stationId: val?.[0]?.station_name?.id,
            recipeId: val?.[0]?.recipe_id?.id,
          })
        : createStationIds?.push({ stationId: e?.id });
      return "";
    });
    setScreenId({ updateStationIds, createStationIds });
    setDoneLoader(false);
    return updateStationIds?.length;
  };
  const screenIds = async (props) => {
    const updateBody = [];
    await directusLoaderLink
      .items("screen_display")
      .readByQuery({
        fields: ["*.*.*"],
        filter: {
          date: { _in: props?.dates },
          location_id: locationId,
        },
      })
      .then((e) => {
        e?.data?.map((res) => {
          updateBody?.push({
            date: res?.date,
            stationId: res?.station_name?.id,
            screen_display_id: res?.id,
            plateImg: res?.app_resultant_image?.id,
          });
        });
        return e?.data;
      })
      .catch((e) => console.log(e));
    return updateBody;
  };
  const recipeBookHandle = async ({ prop: prop, updatedArr: updatedArr }) => {
    if (
      prop?.screenId?.updateStationIds?.length > 0 ||
      prop?.screenId?.createStationIds?.length > 0
    ) {
      const CreateBody = [];
      let CreateUpdateBody = "";
      const CreateBodyIds = [];
      let updateBody = "";
      const updateBodyIds = [];
      const updateCreateBody = [];
      const values = await screenIds({
        dates: updatedArr,
        screenId: prop?.screenId,
      });
      const imageData = await screenIds({
        dates: [format(new Date(prop?.copyDate), DateFormat)],
        screenId: prop?.screenId,
      });
      const recipeId = await directusLoaderLink
        .items("recipe_book")
        ?.createMany(
          prop?.screenId?.createStationIds?.map((e) => {
            return {
              menu_info: "",
              menu_info_1: "",
            };
          })
        )
        .then((re) => {
          const resultIds = re?.raw?.data?.map((e) => e?.id);
          re?.data;
          return resultIds;
        })
        .catch((e) => {
          errorFlag = true;
          console.log(e, "error");
        });
      for (let k = 0; k < updatedArr?.length; k++) {
        for (let f = 0; f < prop?.screenId?.createStationIds?.length; f++) {
          const data = values?.find(
            (day) =>
              day?.date === updatedArr[k] &&
              day?.stationId === prop?.screenId?.createStationIds[f]?.stationId
          );
          const ImageId = imageData?.find(
            (day) =>
              day?.stationId === prop?.screenId?.createStationIds[f]?.stationId
          );
          const updateBody = {
            date: updatedArr[k],
            recipe_id: recipeId[f],
            location_id: locationId,
            station_name: prop?.screenId?.createStationIds[f]?.stationId,
            app_resultant_image: ImageId?.plateImg,
          };
          if (data?.screen_display_id > 0) {
            const objIndex = CreateBodyIds?.findIndex(
              (obj) =>
                obj?.stationId ===
                prop?.screenId?.createStationIds[f]?.stationId
            );
            objIndex !== -1
              ? CreateBodyIds[objIndex]?.Ids?.push(data?.screen_display_id)
              : CreateBodyIds?.push({
                  Ids: [data?.screen_display_id],
                  body: {
                    recipe_id: recipeId[f],
                    app_resultant_image: ImageId?.plateImg,
                  },
                  stationId: prop?.screenId?.createStationIds[f]?.stationId,
                });
          } else {
            const objIndex = CreateBody?.findIndex(
              (obj) =>
                obj?.stationId ===
                prop?.screenId?.createStationIds[f]?.stationId
            );
            objIndex !== -1
              ? CreateBody[objIndex]?.body?.push(updateBody)
              : CreateBody?.push({
                  body: [updateBody],
                  stationId: prop?.screenId?.createStationIds[f]?.stationId,
                });
          }
        }
        prop?.screenId?.updateStationIds?.map((e) => {
          const ImageId = imageData?.find(
            (day) => day?.stationId === e?.stationId
          );
          const updateBody = {
            date: updatedArr[k],
            recipe_id: e?.recipeId,
            location_id: locationId,
            station_name: e?.stationId,
            app_resultant_image: ImageId?.plateImg,
          };
          const data = values?.find(
            (day) =>
              day?.date === updatedArr[k] && day?.stationId === e?.stationId
          );
          if (data?.screen_display_id > 0) {
            const objIndex = updateBodyIds?.findIndex(
              (obj) => obj?.stationId === e?.stationId
            );
            objIndex !== -1
              ? updateBodyIds[objIndex]?.Ids?.push(data?.screen_display_id)
              : updateBodyIds?.push({
                  Ids: [data?.screen_display_id],
                  body: {
                    recipe_id: e?.recipeId,
                    app_resultant_image: ImageId?.plateImg,
                  },
                  stationId: e?.stationId,
                });
          } else {
            const objIndex = updateCreateBody?.findIndex(
              (obj) => obj?.stationId === e?.stationId
            );
            objIndex !== -1
              ? updateCreateBody[objIndex]?.body?.push(updateBody)
              : updateCreateBody?.push({
                  body: [updateBody],
                  stationId: e?.stationId,
                });
          }
        });
      }
      CreateBodyIds?.length > 0 &&
        (await screenDisplayUpdate({
          type: true,
          Ids: CreateBodyIds,
          body: CreateUpdateBody,
        }));
      updateBodyIds?.length > 0 &&
        (await screenDisplayUpdate({
          type: true,
          Ids: updateBodyIds,
          body: updateBody,
        }));
      updateCreateBody?.length > 0 &&
        (await screenDisplayUpdate({
          type: false,
          Ids: "",
          body: updateCreateBody,
        }));
      CreateBody?.length > 0 &&
        (await screenDisplayUpdate({ type: false, Ids: "", body: CreateBody }));
      localStorage.setItem(
        "LastWacthedWeek",
        format(new Date(prop?.copyDate), DateFormat)
      );
    } else {
      toast.error("Please choose a different date.");
    }
  };
  const copyMenuDone = async (prop) => {
    const date = prop?.startDate;
    const YearEnd = endOfYear(date);
    // const EndWeekOfYear = getWeek(addDays(YearEnd, -1));
    const EndWeekOfYear = 52;
    const startWeek = getWeek(date);
    const startYear = date?.getFullYear();
    const startMonth = date?.getMonth() + 1;
    const startDay = date?.getDate();
    const uptoDateYear = prop?.uptoDate !== "" && prop?.uptoDate?.getFullYear();
    const uptoDateMonth =
      prop?.uptoDate !== "" && prop?.uptoDate?.getMonth() + 1;
    const uptoDateDay = prop?.uptoDate !== "" && prop?.uptoDate?.getDate();
    const yearDiff =
      prop?.custom == true && prop?.uptoDate !== "" && uptoDateYear - startYear;
    prop?.Occurrence?.id == 0;
    const Days = [];
    const loop =
      prop?.custom == true
        ? prop?.Occurrence?.id == 1
          ? {
              start: startWeek,
              end:
                prop?.uptoDate !== ""
                  ? getWeek(addDays(prop?.uptoDate, -1))
                  : EndWeekOfYear,
              fullEnd: 52,
              InitialStart: 1,
            }
          : prop?.Occurrence?.id == 2
          ? {
              start: startMonth,
              end:
                prop?.uptoDate !== ""
                  ? uptoDateDay >= startDay
                    ? uptoDateMonth
                    : prop?.uptoDate?.getMonth()
                  : YearEnd?.getMonth() + 1,
              fullEnd: 12,
              InitialStart: 1,
            }
          : prop?.Occurrence?.id == 3
          ? {
              start: startYear,
              end:
                prop?.uptoDate !== ""
                  ? uptoDateYear >= startYear &&
                    uptoDateMonth >= startMonth &&
                    uptoDateDay >= startDay
                    ? uptoDateYear
                    : uptoDateYear - 1
                  : startYear + 5,
              fullEnd: 1,
              InitialStart: startYear,
            }
          : ""
        : prop?.Occurrence?.id == 1
        ? { start: startWeek, end: EndWeekOfYear }
        : prop?.Occurrence?.id == 2
        ? { start: startMonth, end: YearEnd?.getMonth() + 1 }
        : prop?.Occurrence?.id == 3
        ? { start: startYear, end: startYear + 5 }
        : { start: 0, end: 0 };
    var everySplit = 0;
    if (prop?.custom == true) {
      if (
        prop?.uptoDate !== ""
          ? format(prop?.startDate, DateFormat) >=
            format(prop?.uptoDate, DateFormat)
          : false
      ) {
        toast?.error(
          "Please choose the end date greater than the start date to perform further actions."
        );
      } else {
        for (let f = 0; f <= yearDiff; f++) {
          const endL =
            yearDiff > 0
              ? f == yearDiff
                ? loop?.end
                : loop?.fullEnd
              : loop?.end;
          const startL =
            yearDiff > 0
              ? f == 0
                ? loop?.start
                : loop?.InitialStart
              : loop?.start;
          for (let i = startL, j = 0; i <= endL; i++, j++) {
            prop?.Occurrence?.id == 2 &&
              Days.push(
                format(
                  addYears(
                    new Date(
                      `${startYear}-${i < 10 ? `0${i}` : i}-${
                        startDay < 10 ? `0${startDay}` : startDay
                      }`
                    ),
                    f
                  ),
                  DateFormat
                )
              );
            prop?.Occurrence?.id == 3 &&
              Days.push(
                `${i}-${startMonth < 10 ? `0${startMonth}` : startMonth}-${
                  startDay < 10 ? `0${startDay}` : startDay
                } `
              );
            const addedDay = addDays(date, j * 7);
            if (prop?.everyDays?.length > 0 && prop?.Occurrence?.id == 1) {
              prop?.everyDays &&
                prop?.everyDays?.map((day, y) => {
                  const newAddedDay = new Date(addedDay);
                  if (j === 0 && day >= getDay(date)) {
                    if (
                      addDays(
                        startOfISOWeek(new Date(newAddedDay)),
                        day > 0 ? day - 1 : -1
                      ) <= new Date(prop?.uptoDate)
                    ) {
                      Days.push(
                        format(
                          addDays(
                            startOfISOWeek(new Date(newAddedDay)),
                            day > 0 ? day - 1 : -1
                          ),
                          DateFormat
                        )
                      );
                    }
                    return (everySplit = everySplit + 1);
                  }
                  if (j !== 0) {
                    if (
                      addDays(
                        startOfISOWeek(new Date(newAddedDay)),
                        day > 0 ? day - 1 : -1
                      ) <= new Date(prop?.uptoDate)
                    ) {
                      Days.push(
                        format(
                          addDays(
                            startOfISOWeek(new Date(newAddedDay)),
                            day > 0 ? day - 1 : -1
                          ),
                          DateFormat
                        )
                      );
                    }
                    return Days;
                  }
                });
            } else {
              prop?.Occurrence?.id == 1 &&
                Days.push(
                  format(
                    startOfWeek(addDays(date, j * 7), {
                      weekStartsOn: getDay(date),
                    }),
                    DateFormat
                  )
                );
            }
          }
        }
      }
    } else {
      for (let i = loop?.start, j = 0; i <= loop?.end; i++, j++) {
        prop?.Occurrence?.id == 2 &&
          Days.push(
            `${startYear}-${i < 10 ? `0${i}` : i}-${
              startDay < 10 ? `0${startDay}` : startDay
            }`
          );
        prop?.Occurrence?.id == 3 &&
          Days.push(
            `${i}-${startMonth < 10 ? `0${startMonth}` : startMonth}-${
              startDay < 10 ? `0${startDay}` : startDay
            }`
          );
        prop?.Occurrence?.id == 1 &&
          Days.push(
            format(
              startOfWeek(addDays(date, j * 7), { weekStartsOn: getDay(date) }),
              DateFormat
            )
          );
        prop?.Occurrence?.id == 0 &&
          prop?.copyDate !== date &&
          Days.push(format(date, DateFormat));
      }
    }
    const occurrenceArray = [];
    if (prop?.occurrenceCount > 0) {
      if (prop?.everyDays?.length > 0) {
        const resultArray = [];
        const firstSplit =
          prop?.everyDays?.length -
          prop?.everyDays?.indexOf(getDay(new Date(Days[0])));
        for (
          let i = 0;
          i < Days?.length;
          i == 0 ? (i += firstSplit) : (i += prop?.everyDays?.length)
        ) {
          const weekSplit = Days?.slice(
            i,
            i == 0 ? firstSplit : i + prop?.everyDays?.length
          );
          resultArray.push(weekSplit);
        }
        resultArray?.map((e, i) => {
          prop?.occurrenceCount === 1
            ? i % 2 == 0 && occurrenceArray?.push(...e)
            : i % (prop?.occurrenceCount + 1) == 0 &&
              occurrenceArray?.push(...e);
        });
      } else {
        Days?.map((e, i) => {
          prop?.occurrenceCount === 1
            ? i % 2 == 0 && occurrenceArray?.push(e)
            : i % (prop?.occurrenceCount + 1) == 0 && occurrenceArray?.push(e);
        });
      }
    }

    !Days?.includes(format(new Date(prop?.copyDate), DateFormat)) &&
      Days.push(format(new Date(prop?.copyDate), DateFormat));
    const updatedArr = prop?.occurrenceCount > 0 ? occurrenceArray : Days;
    !updatedArr?.includes(format(new Date(date), DateFormat)) &&
      updatedArr.push(format(new Date(date), DateFormat));
    type === "station"
      ? await recipeBookHandle({ prop: prop, updatedArr: updatedArr })
      : await spaActivityAndConferenceHandle({
          prop: prop,
          updatedArr: updatedArr,
        });
    setModalLoader(false);
  };
  const screenDisplayUpdate = async (props) => {
    if (props?.type) {
      const array = props?.Ids;
      for (let i = 0; i < array?.length; i++) {
        await directusLoaderLink
          .items("screen_display")
          ?.updateMany(array?.[i]?.Ids, array?.[i]?.body)
          .then((re) => {})
          .catch((e) => {
            console.log(e);
          });
      }
    } else {
      const array = props?.body;
      for (let i = 0; i < array?.length; i++) {
        await directusLoaderLink
          .items("screen_display")
          ?.createMany(array?.[i]?.body)
          .then((re) => {})
          .catch((e) => {
            console.log(e);
          });
      }
    }
  };
  const DoneHandle = async () => {
    setModalLoader(true);

    const updateProps =
      daysSelect?.length > 0 || week?.trim() || calEndDate !== ""
        ? {
            copyDate: startDate,
            uptoDate:
              calEndDate === ""
                ? daysCopy?.id == 3
                  ? week?.trim()
                    ? new Date(`${startDate?.getFullYear() + 5}-12-31`)
                    : calEndDate
                  : daysSelect?.length > 0 || week?.trim()
                  ? new Date(`${startDate?.getFullYear()}-12-31`)
                  : ""
                : calEndDate,
            occurrenceCount: week?.trim() ? Number(week) : 0,
            Occurrence: daysCopy,
            everyDays: daysSelect,
            custom: true,
            startDate: calStartDate ? calStartDate : startDate,
            screenId,
          }
        : {
            copyDate: startDate,
            Occurrence: daysCopy,
            updateTo: daysCopy?.name,
            custom: false,
            startDate: calStartDate ? calStartDate : startDate,
            screenId,
          };
    if (closeCheck) {
      const IsIdsPresent =
        type === "station"
          ? screenId?.updateStationIds?.length > 0 ||
            screenId?.createStationIds?.length > 0
          : presentData?.length > 0;
      if (IsIdsPresent) {
        await copyMenuDone(updateProps);
        cancelHandle();
        toggleDiv();
      } else {
        toast?.error(
          "The selected copy date doesn't have valid menu information. Kindly check and choose a valid copy date to perform the copy menu of the Day action."
        );
      }
    } else {
      toast?.error(
        "Please choose a valid starting date for updating the menu information."
      );
    }

    setModalLoader(false);
    setRefresh(!refresh);
  };
  const current =
    daysCopy?.id === 1 ? "weeks" : daysCopy?.id === 2 ? "Months" : "years";
  const OccursText = () => {
    let daysText = "";
    daysSelect.length > 0
      ? daysSelect?.map((e, i) => {
          return (daysText +=
            daysSelect?.length > 1 && i == daysSelect?.length - 1
              ? `and ${days?.find((day) => day?.id == e)?.name}`
              : daysSelect?.length > 1
              ? `${days?.find((day) => day?.id == e)?.name}, `
              : days?.find((day) => day?.id == e)?.name);
        })
      : "";
    return (
      <span>
        Occurs
        <span className="font-bold">
          {daysText?.trim() ? " every " : ""}
          {week?.trim() && week != "0" ? ` ${week} ${current} on ` : ""}
          {daysText}
        </span>
        {" starting"}
        {calStartDate
          ? ` ${format(calStartDate, DateFormat)}`
          : ` ${format(startDate, DateFormat)}`}
        {calEndDate ? ` until ${format(calEndDate, DateFormat)}` : ""}
      </span>
    );
  };
  const copyDateDetailsGet = async ({ collectionName, body }) => {
    const response = await directusLoaderLink
      .items(collectionName)
      .readByQuery(body?.[0])
      .then((e) => {
        return e?.data;
      })
      .catch((e) => console.log(e));
    return response;
  };
  const dataDetailsDelete = async ({ collectionName, body }) => {
    const response = await directusLoaderLink
      .items(collectionName)
      .deleteMany(body);
    return response;
  };
  const PresentDetailsIdGet = async () => {
    const CopyDetails = await copyDateDetailsGet({
      collectionName:
        type === "activity"
          ? "activities"
          : type === "conference"
          ? "conference"
          : "spa",
      body: [
        {
          fields: ["*.*.*"],
          filter: {
            date: UrlDate(startDate),
            location_id: locationId,
          },
        },
      ],
    });
    setPresentData(CopyDetails);
    return !!CopyDetails?.[0]?.id ? true : false;
  };
  const spaActivityAndConferenceHandle = async ({ prop, updatedArr }) => {
    if (presentData?.length > 0) {
      const dateArray = updatedArr;
      const DeleteIds = await copyDateDetailsGet({
        collectionName:
          type === "activity"
            ? "activities"
            : type === "conference"
            ? "conference"
            : "spa",
        body: [
          {
            fields: ["id"],
            filter: {
              date: {
                _in: dateArray,
              },
              location_id: locationId,
            },
          },
        ],
      });

      for (let i = 0; i < dateArray?.length; i++) {
        const date = dateArray[i];
        for (let j = 0; j < presentData?.length; j++) {
          if (type === "activity") {
            const { activity_description, activity_name, time } =
              presentData[j];
            const body = {
              date: date,
              activity_name: activity_name,
              activity_description: activity_description,
              time: time,
              location_id: locationId,
            };
            const dayPresents = [0, , 5, 6];
            dayPresents?.includes(getDay(new Date(date))) &&
              (await directusLoaderLink.items("activities")?.createOne(body));
          } else if (type === "conference") {
            const { company_name, room_id } = presentData[j];
            const body = {
              date: date,
              company_name: company_name,
              room_id: room_id?.id,
              location_id: locationId,
            };
            await directusLoaderLink.items("conference")?.createOne(body);
          } else if (type === "spa") {
            const {
              activity_type,
              treatment,
              time,
              price,
              description,
              treatment_1,
              price_1,
              description_1,
              treatment_2,
              price_2,
              description_2,
            } = presentData[j];
            const body = {
              activity_type: activity_type,
              date: new Date(date),
              time: activity_type === "activities" ? time : null,
              location_id: locationId,
              treatment: !!treatment ? treatment : null,
              price: !!price ? price : null,
              description: !!description ? description?.trim() : null,

              treatment_1: !!treatment_1 ? treatment_1 : null,
              price_1: !!price_1 ? price_1 : null,
              description_1: !!description_1 ? description_1?.trim() : null,

              treatment_2: !!treatment_2 ? treatment_2 : null,
              price_2: !!price_2 ? price_2 : null,
              description_2: !!description_2 ? description_2?.trim() : null,
            };
            await directusLoaderLink.items("spa")?.createOne(body);
          }
        }
      }
      !!DeleteIds &&
        DeleteIds?.[0]?.id &&
        (await dataDetailsDelete({
          collectionName:
            type === "activity"
              ? "activities"
              : type === "conference"
              ? "conference"
              : "spa",
          body: DeleteIds?.map((e) => e?.id),
        }));
      localStorage.setItem(
        type === "activity"
          ? "ActivityWacthedWeek"
          : type === "conference"
          ? "ConferenceWacthedWeek"
          : "SpaWacthedWeek",
        new Date(prop?.copyDate)?.getDay() == 0
          ? format(addDays(new Date(prop?.copyDate), -2), "yyyy-MM-dd")
          : format(new Date(prop?.copyDate), DateFormat)
      );
    } else {
      toast.error("Please choose a different date.");
    }
  };
  const ModelOpenHandle = async () => {
    if (closeCheck) {
      const relationId =
        type === "station" ? await ScreenIdGet() : await PresentDetailsIdGet();
      if (relationId) {
        setIsOpen(true);
        setDivVisible(false);
      } else {
        toast?.error(
          "The selected copy date doesn't have valid menu information. Kindly check and choose a valid copy date to perform the copy menu of the Day action."
        );
      }
    } else {
      toast?.error(
        "Please choose a valid starting date for updating the menu information."
      );
    }
  };
  const modalSaveHandle = () => {
    if (
      calEndDate !== ""
        ? UrlDate(calStartDate !== "" ? calStartDate : startDate) >=
          UrlDate(calEndDate)
        : false
    ) {
      toast?.error(
        "Please choose the end date greater than the start date to perform further actions."
      );
    } else {
      setOccurrence(false);
    }
    if (daysSelect?.length > 0 || week?.trim() || calEndDate !== "") {
      setCustomTrue(true);
    }
  };

  return (
    <div>
      <i onClick={toggleDiv} className="cursor-pointer ri-more-2-fill" />
      {isDivVisible && (
        <div
          className={`div-block ${type === "station" ? "" : type} `}
          ref={divRef}
          id={Id}
        >
          <div
            // ${activeMore !== Id ? "inline-block	" : "hidden"  }
            className={`choose_day
              text-black flex flex-col p-4 font-normal absolute min-w-[240px] min-h-[140px] bg-white border-2 right-0 z-10 gap-3`}
          >
            <span className="font-normal text-left text-sm pb-[15px] normal-case">
              Copy "{format(startDate, "EEEE")}"{" "}
              {type === "station" ? "menu of the day" : type} to...
            </span>
            <div
              className={`flex text-sm normal-case justify-between ${
                Occurrence && "pointer-events-none"
              }`}
              onClick={() => setCalenderShow(!calenderShow)}
            >
              <span
                className={
                  calStartDate !== "" ? "show-date text-primary" : "placeholder"
                }
              >
                {calStartDate !== ""
                  ? format(calStartDate, DateFormat)
                  : "Choose a day"}
              </span>
              <span>
                <i className="cursor-pointer ri-arrow-down-s-line" />
              </span>
            </div>
            {DatePickerComponent({
              show: calenderShow,
              setShow: setCalenderShow,
              date: calStartDate,
              setDate: setCalStartDate,
              minDate: startDate,
            })}
            <div className="flex normal-case justify-between relative copy-menu">
              {isHydrated && RepeatSelection(false)}

              {Occurrence && (
                <div className=" flex flex-col p-4 font-normal absolute min-w-[240px] min-h-[140px] bg-white border-2 left-0 top-[26px] z-10 gap-3 custom-date">
                  <div className="flex gap-4 py-[5px] items-center justify-start">
                    <span>Start</span>
                    <div className="date-input border-b border-[#C2C2C2]">
                      <input
                        type="text"
                        readOnly
                        className="pointer-events-none "
                        onChange={(e) => console.log(e)}
                        value={
                          calStartDate !== ""
                            ? format(calStartDate, DateFormat)
                            : ""
                        }
                        placeholder="yyyy-mm-dd"
                      />
                      <i
                        onClick={() => setCalenderShow1(!calenderShow1)}
                        className="ri-calendar-2-line cursor-pointer"
                      ></i>
                      {DatePickerComponent({
                        show: calenderShow1,
                        setShow: setCalenderShow1,
                        date: calStartDate,
                        setDate: setCalStartDate,
                        minDate: startDate,
                      })}
                    </div>
                  </div>
                  <div className="flex gap-4 py-[5px] repeat-select items-center justify-start">
                    <span>Repeat</span>
                    {RepeatSelection(true)}
                  </div>
                  <div className="flex gap-4 py-[5px] items-center justify-start">
                    <span>Every</span>
                    <input
                      type="number"
                      onKeyDown={(evt) =>
                        evt.key === "e" && evt.preventDefault()
                      }
                      value={week}
                      min="0"
                      onChange={(e) => setWeek(e.target.value)}
                      placeholder="0"
                      className="w-[30%] border-b border-[#C2C2C2]"
                    />
                    {week > 1
                      ? current
                      : daysCopy?.id === 1
                      ? "week"
                      : daysCopy?.id === 2
                      ? "Month"
                      : "year"}
                  </div>
                  {daysCopy?.id === 1 && (
                    <div className="flex gap-4 items-center justify-start">
                      <span>On</span>
                      <div className="flex gap-2">{DaysSelection()}</div>
                    </div>
                  )}
                  <div className="flex gap-4 py-[5px] items-center justify-start">
                    <span>End</span>
                    <div className="date-input border-b border-[#C2C2C2]">
                      <input
                        type="text"
                        readOnly
                        className="pointer-events-none"
                        onChange={(e) => console.log(e)}
                        value={
                          calEndDate !== ""
                            ? format(calEndDate, DateFormat)
                            : ""
                        }
                        placeholder="yyyy-mm-dd"
                      />
                      <i
                        onClick={() => setCalenderShow2(!calenderShow2)}
                        className="ri-calendar-2-line cursor-pointer"
                      ></i>
                      {DatePickerComponent({
                        show: calenderShow2,
                        setShow: setCalenderShow2,
                        date: calEndDate,
                        setDate: setCalEndDate,
                        minDate: startDate,
                      })}
                    </div>
                  </div>
                  <p className="text-left block pt-2">{OccursText()}</p>

                  <div className=" flex gap-4 justify-end py-[5px]">
                    <button
                      className="py-1 px-2 border border-primary bg-transparent text-primary text-sm font-medium rounded-md"
                      onClick={(e) => cancelHandle()}
                    >
                      Cancel
                    </button>
                    <button
                      className="py-1 px-2 border border-primary bg-primary text-white text-sm font-medium rounded-md"
                      onClick={() => modalSaveHandle()}
                    >
                      Save
                    </button>
                  </div>
                </div>
              )}
            </div>
            <div className="flex justify-end mt-[15px]">
              <button
                className={`py-1 px-2 border text-white border-primary bg-primary text-sm font-medium rounded-[5px] 
                ${
                  doneLoader
                    ? " bg-[#02393760] border-[#02393760] cursor-default"
                    : ""
                }  `}
                onClick={() => (doneLoader ? null : ModelOpenHandle())}
              >
                {doneLoader ? "Loading..." : "Done"}
              </button>
            </div>
          </div>
        </div>
      )}
      {modalIsOpen && (
        <ConfirmationModal
          modalIsOpen={modalIsOpen}
          setIsOpen={setIsOpen}
          content="Are you sure to paste menu data?"
          callFunction={DoneHandle}
          cancelHandle={cancelHandle}
          Loader={modalLoader}
        />
      )}
    </div>
  );
};

export default ClickOutsideDiv;
