import React, { useState } from "react";
import Modal from "react-modal";

function ConfirmationModal(prop) {
  const [modalIsOpen, setIsOpen] = useState(
    prop?.modalIsOpen === true ? true : false
  );
  const [preventDouble, setPreventDouble] = React.useState(false);

  const customStyles = {
    content: {
      top: "50%",
      left: "50%",
      right: "auto",
      bottom: "auto",
      marginRight: "-50%",
      transform: "translate(-50%, -50%)",
      minWidth: "300px",
      zIndex: 20,
    },
    overlay: {
      zIndex: 20,
    },
  };
  const Handle = async () => {
    setPreventDouble(true);
    await prop?.callFunction();
    prop?.setIsOpen(false);
    setIsOpen(false);
    setPreventDouble(false);
  };
  const closeHandle = () => {
    setIsOpen(false);
    prop?.cancelHandle != undefined && prop?.cancelHandle();
    prop?.CopyMealClickIndex !== undefined && prop?.CopyMealClickIndex("");
  };
  return (
    <Modal
      isOpen={modalIsOpen}
      onRequestClose={() => (prop?.Loader !== undefined ? "" : closeHandle())}
      style={customStyles}
      contentLabel="Logout Modal"
      ariaHideApp={false}
    >
      <div className="p-2">
        <p className="text-black block text-center"> {prop?.content}</p>
        {prop?.Loader !== undefined && preventDouble ? (
          <div className="flex justify-center my-6">
            <button
              type="button"
              className="bg-primary flex items-center gap-1 p-4 text-white font-medium "
              disabled
            >
              <svg
                className="animate-spin -ml-1 mr-3 h-5 w-5 text-white"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
              >
                <circle
                  className="opacity-25"
                  cx="12"
                  cy="12"
                  r="10"
                  stroke="currentColor"
                  strokeWidth="4"
                ></circle>
                <path
                  className="opacity-75"
                  fill="currentColor"
                  d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                ></path>
              </svg>
              <span className="font-medium leading-none">Processing...</span>
            </button>
          </div>
        ) : (
          <div className="flex-row flex mt-6 justify-center gap-3">
            <button
              type="button"
              className="inline-flex  items-center justify-center h-[40px] text-[18px] rounded border bg-transparent border-primary px-4 py-2 text-sm font-bold text-primary shadow-sm focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2"
              onClick={() => closeHandle()}
            >
              Cancel
            </button>
            <button
              type="button"
              onClick={() => preventDouble === false && Handle()}
              className="inline-flex  items-center justify-center h-[40px] text-[18px] rounded border bg-primary border-primary px-4 py-2 text-sm font-bold text-white shadow-sm hover:bg-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2"
            >
              Ok
            </button>
          </div>
        )}
        {prop?.Loader !== undefined && prop?.Loader && preventDouble && (
          <div className="flex-row flex justify-center gap-3">
            Menu data is updating. Please wait for a few seconds. Do not refresh
            the current page during this process.
          </div>
        )}
      </div>
    </Modal>
  );
}

export default ConfirmationModal;
